body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "GothicA1-Thin";
  src: local("GothicA1-Thin"),
   url("./fonts/GothicA1-Thin.ttf") format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "Gotham-Ultra";
  src: local("Gotham-Ultra"),
   url("./fonts/Gotham-Ultra.ttf") format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "AdobeGothicStd-Bold";
  src: local("AdobeGothicStd-Bold"),
   url("./fonts/AdobeGothicStd-Bold.ttf") format("truetype");
  font-weight: normal;
}

