.work{
    height: 100vh;
    background-color: #f2f2f2;
}

.workTitle{
    text-align: center;
    font-family: "Gotham-Ultra";
    color: #1c1c1c;
    font-size: 25vw;
}

.workTitle h1{
    font-size: 7vw;
    transition: 0.5s;
    cursor: pointer;
    user-select: none;
}


.workTitle h1:hover{
    font-size: 8vw;
    animation: color-change 1s infinite alternate-reverse;
    background: #00FFF9;
background: -webkit-linear-gradient(to right, #00FFF9 0%, #5A5AF2 0%, #962CC7 100%, #FF00C1 100%);
background: -moz-linear-gradient(to right, #00FFF9 0%, #5A5AF2 0%, #962CC7 100%, #FF00C1 100%);
background: linear-gradient(to right, #00FFF9 0%, #5A5AF2 0%, #962CC7 100%, #FF00C1 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}
