.japaneseFont2{
    font-family: "AdobeGothicStd-Bold";
    text-align: center;
    color: #fff;
    cursor: pointer;
    user-select: none;   
}

.japaneseFont2 h1{
    font-size: 7vw;
    transition: 0.5s;
}

.japaneseFont2 h1:hover{
    font-size: 8vw;
}

ul.images {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 15px;
  list-style: none;
  margin: 0;
  padding: 0;
}

ul.images > li {
  position: relative;
  padding-top: 66%;
}

ul.images > li img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

@media (max-width: 567px) {
  ul.images {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}
