.social{
    height: 100vh;
    background-color: #f2f2f2;
    background-repeat: no-repeat;
    background-position: left;
}

.socialLinks{
    text-align: center;
    font-family: "Gotham-Ultra";
    color: #1c1c1c;
    font-size:7vw;
    text-transform:uppercase;
    text-decoration: none;
}

.socialLinks a{
    text-decoration: none;
    color: #1c1c1c;
    transition: 0.5s;
}

.socialLinks a:hover{
    animation: color-change 1s infinite alternate-reverse;
    font-size:8vw;
    background: #00FFF9;
background: -webkit-linear-gradient(to right, #00FFF9 0%, #5A5AF2 0%, #962CC7 100%, #FF00C1 100%);
background: -moz-linear-gradient(to right, #00FFF9 0%, #5A5AF2 0%, #962CC7 100%, #FF00C1 100%);
background: linear-gradient(to right, #00FFF9 0%, #5A5AF2 0%, #962CC7 100%, #FF00C1 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
  }
