    transform: skew(0.39deg);
  }
  40% {
    clip: rect(38px, 9999px, 5px, 0);
    transform: skew(0.49deg);
  }
  45% {
    clip: rect(21px, 9999px, 66px, 0);
    transform: skew(0.74deg);
  }
  50% {
    clip: rect(44px, 9999px, 38px, 0);
    transform: skew(0.21deg);
  }
  55% {
    clip: rect(72px, 9999px, 92px, 0);
